import React, { Component } from 'react'
import { Carousel, Autocomplete, Datepicker, Dropdown, Modal, FormSelect } from 'materialize-css/dist/js/materialize.min.js'
import VisitorModel from './VisitorModel'

let autocompleteData = {
    "Goa": null,
    "Varanasi": null,
    "Mumbai ( Shivaji Maharaj Airport )": null,
    "Delhi ( Delhi International Airport )": null
}



class EventCaro extends Component {

    state = {
        destination: '',
        dateOfEvent: null,
        arrivalDate: null,
        departureDate: null,
        selectedEvent: '',
        pax: '',
        hotel: '',
        meals: ''
    }

    componentDidMount(){
        if(window.screen.availWidth > 750){
            const flightCaro = document.querySelector('.event-caro');
            let instance = Carousel.init(flightCaro,{
                fullWidth: true,
                duration: 400
            })

            setInterval(()=>{
                instance.next()
            },6000)
        }

        var fromCity = document.querySelector('.fromCity');
        Autocomplete.init(fromCity,{
            data: autocompleteData,
            limit: 4,
            onAutocomplete: (val)=>{
                this.setState({
                    destination: val
                })
            }
        })

        var checkInDate = document.querySelector('.checkInDate');
        Datepicker.init(checkInDate,{
            defaultDate: new Date(),
            minDate: new Date(),
            onSelect: (selectedDate)=>{
                this.setState({
                    dateOfEvent: selectedDate
                });
            }
        })

        var checkOutDate = document.querySelector('.checkOutDate');
        Datepicker.init(checkOutDate,{
            defaultDate: new Date(),
            minDate: new Date(),
            onSelect: (selectedDate)=>{
                this.setState({
                    arrivalDate: selectedDate
                })
            }
        })


        var checkOutDate = document.querySelector('.departureDate');
        Datepicker.init(checkOutDate,{
            defaultDate: new Date(),
            minDate: new Date(),
            onSelect: (selectedDate)=>{
                this.setState({
                    departureDate: selectedDate
                })
            }
        })

        var eventType = document.querySelectorAll('.event-type');
        FormSelect.init(eventType,{

        })
 
        var userInfo = document.getElementById('user-info');
        this.userDetailsModel =  Modal.init(userInfo,{
            
        })

    }

    submitHotelData = ()=>{
        this.userDetailsModel.open();
    }

    sendAllDataToServer = (userData)=>{
        console.log(userData);
        console.log(this.state);
        this.userDetailsModel.close();
    }

    handleEventChange = (e)=>{
        this.setState({
            selectedEvent: e.target.options[e.target.selectedIndex].value
        })
    }

    handlePaxChange = (e)=>{
        this.setState({
            pax: e.target.options[e.target.selectedIndex].value
        })

    }

    handleHotelChange = (e)=>{
        this.setState({
            hotel: e.target.options[e.target.selectedIndex].value
        })
    }

    handleMealChange = (e)=>{
        this.setState({
            meals: e.target.options[e.target.selectedIndex].value
        })
    }

    render(){
        return (
            <div id="booking-activity">
                <div className="carousel carousel-silder default-caro booking-caro event-caro">
                    <a className="carousel-item" href="#one!">
                        <div className="caro-item" id="caro-1"></div>
                    </a>
                    <a className="carousel-item" href="#two!">
                        <div className="caro-item" id="caro-2"></div>
                    </a>
                    <a className="carousel-item" href="#three!">
                        <div className="caro-item" id="caro-3"></div>
                    </a>  
                </div>

                <div id="flight-model" className="container">
                    <div id="flight-model-content">
                        <div id="flight-iconBox">
                            <i className="material-icons">event_note</i>
                            <span>EVENTS</span>
                        </div>

                        <div className="row">
                            <div className="col s12 m9">
                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">location_on</i>
                                        <input type="text" id="form-city-input" className="autocomplete fromCity" placeholder="Location"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">date_range</i>
                                        <input type="text" className="datepicker checkInDate" placeholder="Date Of Event"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">date_range</i>
                                        <input type="text" className="datepicker checkOutDate" placeholder="Date Of Arrival"></input>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">date_range</i>
                                        <input type="text" className="datepicker departureDate" placeholder="Departure Date"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <select className="event-type" onChange={this.handleEventChange}>
                                            <option value="" disabled selected>Event Type</option>
                                            <option value="Corporate Meeting">Corporate Meeting</option>
                                            <option value="Conference">Conference</option>
                                            <option value="Exibition">Exibition</option>
                                            <option value="Wedding">Wedding</option>
                                            <option value="Incentive Event">Incentive Event</option>
                                            <option value="Leasure">Leasure</option>
                                            <option value="Other">Other</option>
                                        </select>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <select className="event-type" onChange={this.handlePaxChange}>
                                            <option value="" disabled selected>Pax</option>
                                            <option value="Mini/0-9">Mini/0-9</option>
                                            <option value="Max/9+">Max/9+</option>
                                            <option value="Jumbo/100+">Jumbo/100+</option>
                                        </select>
                                    </div>      

                                </div>

                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <select className="event-type" onChange={this.handleHotelChange}>
                                            <option value="" disabled selected>Hotel/Resort</option>
                                            <option value="Economy/3star">Economy/3star</option>
                                            <option value="Premium/4star">Premium/4star</option>
                                            <option value="Luxuary/5star">Luxuary/5star</option>
                                        </select>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <select className="event-type" onChange={this.handleMealChange}>
                                            <option value="" disabled selected>Meals</option>
                                            <option value="All Inclusive">All Inclusive</option>
                                            <option value="CM Customized Menu">CM Customized Menu</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <div className="col s12 m2 offset-m1">
                                <button onClick={this.submitHotelData} className="waves-effect waves-light btn blue" id="search">Search</button>
                            </div>
                        </div>
                    </div>
                </div>
                <VisitorModel sendData={this.sendAllDataToServer} />
            </div>
        )
    }
}

export default EventCaro;